@import "../../../src/assets/style/variables";
@import "../../../src/assets/style/mixin";
@import "../../../src/assets/style/fonts";

.L-contacts-container {
  width: 100%;
  height: 100%;
  padding-top: 200px;
  position: relative;

  .L-map-container {
    position: relative;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;

    .L-map-box {
      width: 100%;
      position: relative;
      max-width: 600px;
      height: 600px;
      border: 25px solid #f59dda;
      border-radius: 50%;
      margin: 0 auto;
      overflow: hidden;

      .L-map {
        width: 550px;
        height: 550px;
        transform: scale(1.2);
      }
    }

    .L-map-compass {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      position: absolute;
      font-family: SansSerif, serif;
      top: -10px;
      background-color: black;
      color: white;
      font-size: 40px;
      z-index: 1;
    }

    .L-map-mission {
      position: absolute;
      left: -15px;
      color: white;
      font-family: Passion, serif;
      -webkit-text-stroke: 2px black;
      font-size: 80px;
      z-index: 1;
    }
  }

  .L-contacts-box {
    position: absolute;
    top: 150px;
    right: 20px;
    width: max-content;
    font-size: 30px;
    display: flex;

    .L-info-block {
      margin-right: 30px;

      .L-time-container {
        text-align: right;
        padding-right: 10px;
      }
      .L-tel-number {
        .tel {
          display: flex;
          align-items: center;

          p {
            -webkit-text-stroke: 1.3px black;
            color: #02f79b;
            margin: 0 10px;
          }

          i {
            color: #00ff00;
            -webkit-text-stroke: 1px black;
            font-size: 25px;
            margin-right: 5px;
          }
        }
      }
    }

    .L-socials-block {
      width: 120px;
      height: 120px;
      background: rgb(253, 133, 253);
      background: linear-gradient(
        0deg,
        rgba(253, 133, 253, 1) 0%,
        rgba(95, 194, 236, 1) 100%,
        rgba(58, 189, 252, 1) 100%
      );
      border-radius: 0 15px 15px 15px;
      position: relative;
      .L-slider-cont {
        position: absolute;
        width: 110px;
        height: 90px;
        top: 3.5%;
        left: 3.5%;
        background: rgba($color: white, $alpha: 0.3);
        border-radius: 0 15px 15px 15px;
        box-shadow: 0 0 -15px #0066ff;
        .sliderBlock {
          font-size: 60px;
          animation: socialMedia 3.1s ease-in reverse infinite;
          a {
            i {
              margin-left: 25px;
              transition: 0.3s;

              &:hover {
                transform: scale(1.5);
                transition: 0.3s;
              }
            }
          }
        }
        .Gmail {
          color: black;
          padding: 0 2px;

          &:hover {
            color: #d14836;
            border-radius: 5px;
            background-color: white;
          }
        }

        .Github {
          color: $black;
          &:hover {
            border-radius: 50px;
            background-color: white;
          }
        }

        .Linkedin {
          color: $black;
          &:hover {
			border-radius: 5px;
            background-color: white;
          }
        }

        .Telegram {
          color: $black;
          transform: scale(1.1);
          &:hover {
            border-radius: 50px;
            background-color: white;
            color: #0066ff;
          }
        }
      }
    }
  }
}
@keyframes socialMedia {
  0% {
    transform: rotateX(0deg) rotateY(0deg);
  }

  100% {
    transform: rotateX(0deg) rotateY(360deg);
  }
}
